import React, { Component } from 'react';
import { Card, CardImg, CardText, CardBody, CardTitle, Breadcrumb, BreadcrumbItem, Button,Label, Col, Row, Modal, ModalHeader, ModalBody } from 'reactstrap';
import {Link } from 'react-router-dom';
import {Control, LocalForm, Errors} from 'react-redux-form';
import { Loading } from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => val && (val.length >= len);
   
class CommentForm extends Component{
    constructor(props){
        super(props);
        this.state={
            isModalOn: false
        };
        this.toggleModal= this.toggleModal.bind(this);
    }

    toggleModal(){
        this.setState({
            isModalOn: !this.state.isModalOn
        });
    }

    handleSubmit(values){
        this.toggleModal();
        this.props.postComment(this.props.dishId, values.location, values.author, values.comment);
    }

    render(){
        return(
            <div>
                <Button outline onClick={this.toggleModal}>
                    <span className="fa fa-pencil fa-lg"></span> Submit Suggestion
                </Button>
                <Modal isOpen = {this.state.isModalOn} toggle={this.toggleModal}>
                    <ModalHeader toggle={this.toggleModal}><span className="text-dark" >Submit Suggestion</span></ModalHeader>
                    <ModalBody>
                        <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
                            <Row className="form-group">
                                <Col>
                                    <Label htmlFor="author">Your Name</Label>                               
                                    <Control.text model=".author" id="author" name="author" 
                                    placeholder="Your Name"
                                    className="form-control"
                                    validators={{
                                        required, minLength: minLength(2), maxLength:maxLength(15)
                                    }}/>
                                    <Errors  className="text-danger" 
                                        model=".author"
                                        show="touched"
                                        messages={{
                                            required: 'Required',
                                            minLength: 'Must be greater than 2 characters',
                                            maxLength: 'Must be 15 characters or less'
                                    }}/>
                                </Col>                                
                            </Row> 
                            <Row className="form-group">
                                <Col>
                                    <Label htmlFor="location">Location</Label>                               
                                    <Control.text model=".location" id="location" name="location" 
                                    placeholder="Location"
                                    className="form-control"
                                    validators={{
                                        required, minLength: minLength(2), maxLength:maxLength(15)
                                    }}/>
                                    <Errors  className="text-danger" 
                                        model=".location"
                                        show="touched"
                                        messages={{
                                            required: 'Required',
                                            minLength: 'Must be greater than 2 characters',
                                            maxLength: 'Must be 15 characters or less'
                                    }}/>
                                </Col>                                
                            </Row>                                                     
                            <Row className="form-group">
                                <Col>
                                    <Label htmlFor="comment">Suggestion</Label>                               
                                    <Control.textarea model=".comment" id="comment" name="comment" 
                                    rows="6"
                                    className="form-control"/>
                                </Col>                                
                            </Row>                           
                            <Button type="submit" color="primary">
                                Submit
                            </Button>                               
                        </LocalForm>
                    </ModalBody>
                </Modal>
                {document.body.style.background ="linear-gradient(133deg, rgba(0,0,0,1) 35%, rgba(69,18,18,1) 87%)"}
             </div>

        );
    }
}

function RenderDish({dish}){
        if(dish!=null)
            return(
                <div  className="col-12 col-md-5 m-1">    
          <FadeTransform
                in
                transformProps={{
                    exitTransform: 'scale(0.5) translateY(-50%)'
                }}>
              <Card className="bg-dark">
                    <CardImg top src={baseUrl + dish.image} alt={dish.name} />
                    <CardBody>
                        <CardTitle className="text-danger">{dish.name}</CardTitle>
                        <CardText>{dish.description}</CardText>
                    </CardBody>
              </Card>
         </FadeTransform>
                </div>
            );        
        else
            return(<div></div>);
        
    }

    function RenderComments({comments,postComment,dishId}){
        if(comments!=null)
            return(
                <div className="col-12 col-md-5 m-1">
                <h4>Comments</h4>
                <ul className="list-unstyled">
                        <Stagger in>
                                {comments.map((comment) => {
                                    return (
                                        <Fade in>
                                        <li key={comment.id}>
                                        <p>{comment.comment}</p>
                                        <p>-- {comment.author} ,{comment.location}, {new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(new Date(Date.parse(comment.date)))}</p>
                                        </li>
                                        </Fade>
                                    );
                                })}
                    </Stagger>
                </ul>
            <CommentForm dishId={dishId} postComment={postComment} />
            </div>
            );
        else
            return(
             <div></div>
            );
    }

    const DishDetail = (props) => { 
        if (props.isLoading) {
            return(
                <div className="container">
                    <div className="row">            
                        <Loading />
                    </div>
                </div>
            );
        }
        else if (props.errMess) {
            return(
                <div className="container">
                    <div className="row">            
                        <h4>{props.errMess}</h4>
                    </div>
                </div>
            );
        }
        else if (props.dish != null){
                return(
                    <div className="container">
                        <div className="row">
                            <Breadcrumb>
                                <BreadcrumbItem><Link to= '/menu'>Menu</Link ></BreadcrumbItem>
                                <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
                            </Breadcrumb>
                            <div className="col-12">
                                <h3>{props.dish.name}</h3>
                                <hr/>
                            </div>
                        </div>
                            <div className="row">                           
                                <RenderDish dish = {props.dish} />        
                                <RenderComments comments={props.comments}
                                             postComment={props.postComment}
                                             dishId={props.dish.id} />                      
                            </div>
                    </div>
                );
            }
            else{
                return(
                    <div></div>
                );
            }        
        }


export default DishDetail;